<template>
    <div class="container">
        <div class="header">
            <h1>Hello,</h1>
            <p class="title">请认证您的信息</p>
        </div>

        <div class="field-group">
            <van-cell-group :border="false">
                <van-field class="field"
                           v-model="phone"
                           label="+86"
                           placeholder="请输入手机号"
                           input-align="left"
                           type="number"
                           center
                           clearable
                >
                    <p class="label" slot="label">+86</p>
                </van-field>
                <van-field class="field"
                           v-model="sms"
                           type="number"
                           center
                           clearable
                           input-align="left"
                           placeholder="验证码"
                >
                    <van-button :class="smsClass" :disable="!sending" @click="onSms" slot="button" size="small"
                                type="primary">{{sendingText}}
                    </van-button>
                </van-field>
            </van-cell-group>
        </div>

        <van-button @click="onSubmit" class="confirm">下一步</van-button>

    </div>
</template>

<script>
    import {Lazyload, CellGroup, Field, SubmitBar, Button} from 'vant';
    import store from '../../storage'
    import {FETCH_OPENID, DRIVER_VERIFY, CHECK_CODE, DRIVER_AUTH} from '../../common/config'
    import {get, post} from '../../common/http'
    import {Toast} from 'vant';

    export default {
        components: {
            [Lazyload.name]: Lazyload,
            [CellGroup.name]: CellGroup,
            [SubmitBar.name]: SubmitBar,
            [Field.name]: Field,
            [Button.name]: Button
        },

        data() {
            return {
                sending: false,
                sendingText: '发验证码',
                time: 0,
                data: {},
                phone: '',
                sms: '',
                from: ''
            };
        },

        computed: {
            smsClass() {
                if (!this.sending) {
                    return 'sms'
                } else {
                    return 'sms disable'
                }
            }
        },

        methods: {
            onSubmit() {
                const params = {phone: this.phone, code: this.sms, tags: 1, openId: this.data.openid}
                post(DRIVER_AUTH, params, res => {
                    let data = {...res, openId: this.data.openid}
                    store.save(data)
                    this.$router.push({name: 'extra', query: {from: this.from}})
                })
            },
            onSms() {
                if (!this.sending) {
                    this.sending = true;
                    this.time = 35;
                    this.timer();
                    const tags = 1
                    const params = {phone: this.phone, tags}
                    post(CHECK_CODE, params)
                }
            },
            timer() {
                if (this.time > 0) {
                    this.time--;
                    this.sendingText = this.time + "";
                    setTimeout(this.timer, 1000);
                } else {
                    this.time = 0;
                    this.sendingText = "发验证码";
                    this.sending = false
                }
            },
            verifyData(data) {
                this.data = data
                if (data.status === 4) {
                    // info
                    this.$router.push({name: 'info', query: {from: this.from}})
                } else if (data.status === 3) {
                    // extra
                    this.$router.push({name: 'extra', query: {from: this.from}})
                }
            }
        },
        created() {
            this.from = this.$route.query.from
            const data = store.fetch()
            if (!this.from) {
                Toast.fail("请先进行注册")
            }
            if (data && data.hasOwnProperty("openid")) {
                this.data = data
                // if (data.hasOwnProperty("id")) {
                //     this.verifyData(data)
                // } else {
                const params = {openId: data.openid}
                post(DRIVER_VERIFY, params, res => {
                    let resData = res;
                    resData.openid = this.data.openid
                    store.save(resData)
                    this.verifyData(resData)
                })
                // }
            } else {
                const code = store.urlParam('code')
                if (code) {
                    const params = {code}
                    get(FETCH_OPENID, params, res => {
                        this.data.openid = res.openId;
                        store.save(data)
                        window.console.log(res, 'fetch openid', this.data)
                        let param = {openId: this.data.openid}
                        //let param = {openId: 'oqZWxxPpZ6IRbjheJby5mSGNkI6w'}
                        post(DRIVER_VERIFY, param, res => {
                            let resData = res;
                            resData.openid = this.data.openid
                            store.save(resData)
                            this.verifyData(resData)
                        })
                    });
                } else {
                    Toast.fail("无法获得授权信息，请退出重试")
                }
            }

        }

    }
</script>

<style lang="less">

    @import "../../assets/common.less";

    .field-group {
        padding: 0.82rem;
        .label {
            /*width: 0.59rem;*/
            font-size: 0.34rem;
            font-weight: normal;
            font-stretch: normal;
            letter-spacing: 0px;
            color: #4e4e4e;
        }
    }
</style>
